import React, {Component} from 'react';
import {PageHeader, Descriptions, Tag, Tooltip, Input, message, notification, Button} from 'antd';
import PubSub from "pubsub-js";
import moment from "moment";
import {PlusOutlined, SaveOutlined} from '@ant-design/icons';
import {connect} from "react-redux";
import {request, synchronizedRequest} from "../../../../Utils/request";
import {addTagListAction, deleteTagListAction} from "../../../../redux/actions/updateTagListAction";
import './index.css'
class MyPageHeader extends Component {

    state = {
        NoteInfo:{
            subTitle:'',
            CreatedTime:'',
            UpdatedTime:'',
            //当前notebook的tag数组
            Tags:[],
            parentTitle:'',
            // NoteUsn:0,
            NoteId:"",
            NotebookId:'',
            Usn:0
        },
        //总状态的数组用于寻找Usn来进行更新操作
        inputVisible: false,
        inputValue: '',
        editInputIndex: -1,
        editInputValue: '',
        //告诉用户关于删除Tag的操作
        hasGiveTagInformation:false
    }
    componentDidMount() {
        PubSub.subscribe("NoteInfo",(msg,data) => {
            let{NoteInfo} = this.state;
            switch (data.type){
                case "click":
                    this.setState({NoteInfo:data.NoteInfo})
                    break;
                case "update":
                    //只有是当前选中的NoteInfo才去更新显示信息
                    if(NoteInfo && NoteInfo.NoteId === data.NoteInfo.NoteId){
                        console.log("BeforePageHeaderUpdate",NoteInfo)
                        let{UpdatedTime,subTitle,Usn} = data.NoteInfo
                        // let{UpdatedTime,subTitle,Usn} = data.NoteInfo
                        // NoteInfo = {...NoteInfo,UpdatedTime,subTitle,Usn}
                        NoteInfo = {...NoteInfo,UpdatedTime,subTitle,Usn}
                        this.setState({NoteInfo});
                    }
                   /* this.setState(({NoteInfo}) =>{
                        console.log("BeforePageHeaderUpdate",NoteInfo)
                        let{UpdatedTime,subTitle,Usn} = data.NoteInfo
                        NoteInfo = {...NoteInfo,UpdatedTime,subTitle,Usn}
                        return{
                            NoteInfo
                        }
                    })*/
                    break;
                case "Tag":
                    this.setState({NoteInfo:{}});
            }
        })
    }
    componentWillUnmount() {
        PubSub.unsubscribe("NoteInfo");
        this.setState({},()=>{
            return false
        });
    }

    /**
     *  当所有的笔记中都不没有同一个标签时，标签会被标记为IsDeleted:true
     * @param removedTag
     * @returns 删除Tag的回调
     */
    handleClose = async removedTag => {
        let {NoteInfo,hasGiveTagInformation} = this.state;
        let {NoteId,Tags,Usn} = NoteInfo;
        let {TagList} = this.props
        //总状态的Tag
        let TagObj = TagList.find(ele => ele.Tag === removedTag);
        //当前页头的Tag
        let currentNewTags = Tags.filter(tag => tag !== removedTag);
        //给的api在空标签状态下需要放一个空串否则过不了
        // if (currentNewTags.length === 0) currentNewTags[0] = '';
        console.log(currentNewTags);
        try{
            let result;
            if (TagObj){
                result = await synchronizedRequest([
                    {url:"tag/deleteTag",data:{tag:removedTag,token:this.props.Token,usn:TagObj.Usn},method:"post"},
                    {url:"note/updateNote",data:{Tags: [...(currentNewTags.length === 0 ? [""] : currentNewTags)],token:this.props.Token,NoteId,Usn},method:"post"}
                ])
                NoteInfo.Usn = result[1].Usn;
                NoteInfo.UpdatedTime = result[1].UpdatedTime;
                // Usn = updatedNote.Usn
            }else {
                result = await request("note/updateNote",{Tags: [...(currentNewTags.length === 0 ? [""] : currentNewTags)],token:this.props.Token,NoteId,Usn})
                // NoteInfo.Usn = result.Usn;
            }

            console.log("删除tag后的更新",result);
            if (!result.hasOwnProperty("Ok")){
                //在页面修改时要重新变为空数组
                NoteInfo.Tags = currentNewTags
                // this.props.deleteTagListAction(TagObj)
                if (!hasGiveTagInformation){
                    const key = `open${Date.now()}`;
                    const btn = (
                        <Button type="primary" size="small" onClick={() => notification.close(key)}>
                            确认
                        </Button>
                    )
                    notification.open({
                        message: '提示',
                        duration:0,
                        description:
                            '当所有笔记中都不存在当前标签时,即该标签已经完全被移除时,下一次更新标签导航将不会显示该标签',
                        btn,
                        key
                    })
                }
                this.setState({NoteInfo,hasGiveTagInformation:true});
                PubSub.publish("updateNote",NoteInfo)
                // PubSub.publish("updateEditor",NoteInfo)
            }else {
                message.warn("删除失败请稍后再试!")
            }
        }catch (e){
            console.log(e);
            message.warn("删除失败请稍后再试!")
        }
    };

    showInput = () => {
        this.setState({ inputVisible: true }, () => this.input.focus());
    };

    handleInputChange = e => {
        this.setState({ inputValue: e.target.value });
    };
    /**
     *
     * @returns 更新Tag操作
     */
    handleInputConfirm = async () => {
        let {NoteInfo,inputValue} = this.state;
        let {NoteId,Tags,Usn} = NoteInfo;
        let {TagList} = this.props;
        if (inputValue && Tags.indexOf(inputValue) === -1) {
            try {
                //发请求添加tag并且通知更新当前note的tag信息
                let result = await synchronizedRequest([
                    {url:"tag/addTag",data:{tag:inputValue,token:this.props.Token},method:"post"},
                    {url:"note/updateNote",data:{Tags: [...Tags,inputValue],token:this.props.Token,NoteId,Usn},method:"post"}
                ])
                // NoteInfo.Usn = result[1].Usn;
                NoteInfo.UpdatedTime = result[1].UpdatedTime;
                //说明tag修改成功且对应的note也修改成功
                if (!result.hasOwnProperty("Ok")){
                    console.log(result);
                    // PubSub.publish("updateTagList",{type:"add",Tag:result[0]})
                    Tags = [...Tags, inputValue];
                    //result[0] = {Tag...}对象信息
                    if(!TagList.find(ele => ele.Tag === inputValue)){
                        this.props.addTagListAction([result[0]])
                    }
                    NoteInfo.Tags = Tags;
                    this.setState({
                        NoteInfo,
                    });
                }
            }catch (e){
                message.warn("网络异常请稍后再试")
            }
        }else {
            message.info("请勿添加相同的标签")
        }
        PubSub.publish("updateNote",NoteInfo);
        PubSub.publish("updateEditor",NoteInfo);

        this.setState({
            inputVisible: false,
            inputValue: '',
        });
    };

    handleEditInputChange = e => {
        this.setState({ editInputValue: e.target.value });
    };

    handleEditInputConfirm = () => {
        this.setState(({ NoteInfo, editInputIndex, editInputValue }) => {
            const newTags = [...NoteInfo.Tags];
            newTags[editInputIndex] = editInputValue;
            NoteInfo.Tags = newTags;
            return {
                NoteInfo,
                editInputIndex: -1,
                editInputValue: '',
            };
        });
    };

    saveInputRef = input => {
        this.input = input;
    };

    saveEditInputRef = input => {
        this.editInput = input;
    };


    render() {
        let{NoteInfo, inputVisible, inputValue, editInputIndex, editInputValue} = this.state;
        let{subTitle,CreatedTime,UpdatedTime,parentTitle,Tags} = NoteInfo;
        return (
            <div className="Editor_Header">
                <PageHeader
                    className="site-page-header"
                    title={parentTitle ?  parentTitle : 'BreadNote' }
                    subTitle={subTitle ? subTitle : 'Welcome'}
                    style={{
                        height:100,
                        padding:"8px 24px"
                    }}
                    extra={[
                        <Button key="save" icon={<SaveOutlined />} onClick={() => PubSub.publish("NoteInfo",{type:"content"})}>
                            保存
                        </Button>,
                    ]}
                >
                    <Descriptions size="small" column={2}>
                        <Descriptions.Item label="CreatedTime">{CreatedTime ? moment(CreatedTime).format("YYYY-MM-DD HH:mm:ss") : ''}</Descriptions.Item>
                        <Descriptions.Item label="UpdatedTime">{UpdatedTime ? moment(UpdatedTime).format("YYYY-MM-DD HH:mm:ss") : ''}</Descriptions.Item>
                    </Descriptions>
                </PageHeader>
                {parentTitle ? <div style={{
                    display:"flex",
                    alignItems:'center',
                    width:"100%",
                    padding:"0 20px",
                    height:20
                }}>

                    {Tags.map((tag, index) => {
                        if (editInputIndex === index ) {
                            return (
                                <Input
                                    ref={this.saveEditInputRef}
                                    key={tag}
                                    size="small"
                                    className="tag-input"
                                    value={editInputValue}
                                    onChange={this.handleEditInputChange}
                                    onBlur={this.handleEditInputConfirm}
                                    onPressEnter={this.handleEditInputConfirm}
                                />
                            );
                        }

                        const isLongTag = tag.length > 20;
                        const tagElem = (
                            <Tag
                                className="edit-tag"
                                key={tag}
                                closable={true}
                                onClose={() => this.handleClose(tag)}
                            >
                                  <span
                                      onDoubleClick={e => {
                                          if (index !== 0) {
                                              this.setState({ editInputIndex: index, editInputValue: tag }, () => {
                                                  this.editInput.focus();
                                              });
                                              e.preventDefault();
                                          }
                                      }}
                                  >
                                    {isLongTag ? `${tag.slice(0, 20)}...` : tag}
                                  </span>
                            </Tag>
                        );
                        return isLongTag ? (
                            <Tooltip title={tag} key={tag}>
                                {tagElem}
                            </Tooltip>
                        ) : (
                            tagElem
                        );
                    })}
                    {inputVisible && (
                        <Input
                            ref={this.saveInputRef}
                            type="text"
                            size="small"
                            className="tag-input"
                            value={inputValue}
                            onChange={this.handleInputChange}
                            onBlur={this.handleInputConfirm}
                            onPressEnter={this.handleInputConfirm}
                        />
                    )}
                    {!inputVisible && (
                        <Tag className="site-tag-plus" onClick={this.showInput}>
                            <PlusOutlined /> New Tag
                        </Tag>
                    )}
                </div> : <></>}
            </div>
        );
    }
}

//暴露组件
//state将会是store中的总状态需要分别拿出来
export default connect(state => ({TagList:state.TagList}),{
    //api层面的优化，会自动加入dispatch
    deleteTagListAction,
    addTagListAction,
    // requestTagListAction
})(MyPageHeader);