import React, {Component} from 'react';
import {Form, Col, Row, Input, Select, DatePicker, Button, message} from 'antd'
const { Option } = Select;
class FromNoteBookForm extends Component {
    handleOnFinish = async (values) => {
       /* if (values.type === 'MarkDown'){
            message.warn("当前不支持MarkDown编辑器");
            return
        }*/
        // console.log(values);
        if (!values.Tags) values.Tags = [""];
        this.props.onFinish(values);
        // console.log(values)
    }
    render() {
        // const { tags, inputVisible, inputValue ,children} = this.state;
        let{NoteBookList,TagList} = this.props
        return (
            <Form layout="vertical"
                  hideRequiredMark
                  initialValues={{"type":"MarkDown"}}
                  onFinish={this.handleOnFinish}
            >
                <Row gutter={16}>
                    <Col span={12}>
                        <Form.Item
                            name="NotebookId"
                            label="笔记本名称"
                            rules={[{ required: true, message: '请选择一个笔记本' }]}
                        >
                            <Select
                                showSearch
                                style={{ width: 200 }}
                                placeholder="选择笔记本"
                                optionFilterProp="children"
                                /*onChange={onChange}
                                onFocus={onFocus}
                                onBlur={onBlur}
                                onSearch={onSearch}*/
                                filterOption={(input, option) =>
                                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                            >
                                {
                                    NoteBookList.map(ele => {
                                        return (<Option key={ele.NotebookId} value={ele.NotebookId}>{ele.Title}</Option>)
                                    })
                                }
                        </Select>
                            {/*<Input placeholder="默认在当前选中的笔记本下创建" />*/}
                            {/*<Input placeholder={this.props.NoteBookName ? this.props.NoteBookName : "你尚未选择笔记本"}/>*/}
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Form.Item
                            name="Title"
                            label="笔记名称"
                            rules={[{ required: true, message: '请输入笔记名称' }]}
                        >
                            <Input placeholder="请输入笔记名称..."/>
                        </Form.Item>
                    </Col>
                </Row>
                <Row gutter={16}>
                    <Col span={12}>
                        <Form.Item
                            name="type"
                            label="笔记类型"
                            rules={[{ required: true, message: '请选择你要创建的笔记类型' }]}
                        >
                            <Select>
                                <Option value="Common">普通笔记</Option>
                                <Option value="MarkDown">MarkDown</Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Form.Item
                            name="Tags"
                            label="标签"
                            // rules={[{ required: true, message: 'Please enter url' }]}
                        >
                            <Select mode="tags" style={{ width: '100%' }} placeholder="在这里输入你想要的标签">
                                {TagList.map(ele => {
                                    return (
                                        <Option key={ele.TagId} value={ele.Tag}>{ele.Tag}</Option>
                                    )
                                })}
                            </Select>
                        </Form.Item>
                    </Col>
                </Row>
                <Row gutter={16}>
                    <Col span={24}>
                        <Form.Item
                            name="CreatedTime"
                            label="日期"
                            rules={[
                                {
                                    required: true,message:'请选择创建日期!'
                                },
                            ]}
                        >
                            <DatePicker showTime style={{width:"100%"}}/>
                        </Form.Item>
                    </Col>
                </Row>
                {/*<Row gutter={16}>
                    <Col span={24}>
                        <Form.Item
                            name="Content"
                            label="笔记内容"
                            rules={[
                                {
                                    required: false,
                                },
                            ]}
                        >
                            <Input.TextArea rows={4} placeholder="你可以在此输入一些基本的内容或者默认不添加内容" />
                        </Form.Item>
                    </Col>
                </Row>*/}
                <Row gutter={16}>
                    <Col span={24}>
                        <Form.Item>
                            <div style={{
                                float:'right'
                            }}>
                                <Button htmlType="button" onClick={this.props.onCancel}  style={{ marginRight: "10px" }}>
                                    取消
                                </Button>
                                <Button type="primary" htmlType="submit" >
                                    确认
                                </Button>
                            </div>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        );
    }
}

export default FromNoteBookForm;