import React, {Component,Fragment} from 'react';
// 引入编辑器组件
import BraftEditor from 'braft-editor'
import ReactMarkdown from "react-markdown";
import rehypeHighlight from 'rehype-highlight'
import 'highlight.js/styles/github.css';
// 引入编辑器样式
import 'braft-editor/dist/index.css'
import 'braft-extensions/dist/code-highlighter.css'
import CodeHighlighter from 'braft-extensions/dist/code-highlighter'
import 'prismjs/components/prism-java'
import PubSub from "pubsub-js";
import {Button, message, notification} from "antd";
import {BulbOutlined} from "@ant-design/icons";
import moment from "moment";
import TextArea from "antd/es/input/TextArea";
import './index.css'
import {request} from "../../../../Utils/request";
// 通过opitons.

const options = {
    syntaxs: [
        {
            name: 'JavaScript',
            syntax: 'javascript'
        }, {
            name: 'HTML',
            syntax: 'html'
        }, {
            name: 'CSS',
            syntax: 'css'
        }, {
            name: 'Java',
            syntax: 'java',
        }
    ]
}

BraftEditor.use(CodeHighlighter({
    includeEditors: ['editor-with-code-highlighter']
}))
BraftEditor.use(CodeHighlighter(options))
class MyBraftEditor extends Component {
    state = {
        // 创建一个空的editorState作为初始值
        editorState: BraftEditor.createEditorState(null),
        NoteInfoList:{},
        currentNote:{},
        // hasChange:false
    }
    async componentDidMount () {
        console.log(moment(Date.now()).format("YYYY-MM-DD HH:mm:ss"));
        const key = `open${Date.now()}`;
        const btn = (
            <Button type="primary" size="small" onClick={() => notification.close(key)}>
                确认
            </Button>
        )
        setTimeout(()=>{
            notification.open({
                message: '提示',
                duration:0,
                description:
                    '您可以点击页头保存按钮或者按下ctrl+s键来保存笔记,笔记内容暂不支持自动保存',
                btn,
                key,
                icon:<BulbOutlined />
            })
        },10000)
        PubSub.subscribe('NoteInfo',async (msg,data) => {
            let{NoteInfoList,currentNote,editorState} = this.state;
            let Content = null
            switch (data.type) {
                case "click":
                    // console.log("点击",data.NoteInfo);
                    //说明切换到了其他的笔记
                    if (currentNote && currentNote.hasOwnProperty("NoteId") && currentNote.NoteId !== data.NoteInfo.NoteId){
                        console.log("点击了其他组件")
                        NoteInfoList = [...NoteInfoList.filter(ele => ele.NoteId !== currentNote.NoteId),currentNote];
                        this.setState({NoteInfoList});
                    }
                    currentNote = NoteInfoList.find(ele => ele.NoteId === data.NoteInfo.NoteId)
                    if(currentNote && currentNote.hasOwnProperty("Content")){
                        //说明当前存在组件
                        editorState = BraftEditor.createEditorState(currentNote.Content)
                    }else {
                        let result = await request("note/getNoteContent", {
                            token: this.props.Token,
                            noteId: data.NoteInfo.NoteId
                        }, "post")
                        if (!result.hasOwnProperty("Ok")) {
                            //首次点击当前组件
                            Content = result.Content === "" ? null : result.Content;
                            currentNote = data.NoteInfo;
                            currentNote.Content = Content;
                            editorState = BraftEditor.createEditorState(Content)

                        } else {
                            message.warn("获取内容失败!")
                        }
                    }
                    this.setState({
                        currentNote,
                        editorState
                    })
                    break;
                /*case "update":
                    if(data.NoteInfo.NoteId === currentNote.NoteId){
                        currentNote.Usn = data.NoteInfo.Usn;
                        this.setState({currentNote});
                    }

                    break;*/
                case "content":
                    await this.submitContent()
            }
        })

       /* PubSub.subscribe("updateEditor",(msg,data) => {
            console.log("编辑器更新")
            this.setState(({currentNote}) => {
                currentNote.Usn = data.Usn;
                return{currentNote}
            })
        })*/
        this.setState({NoteInfoList:this.props.NoteList})
        // 假设此处从服务端获取html格式的编辑器内容
        // const htmlContent = await fetchEditorContent()
        // 使用BraftEditor.createEditorState将html字符串转换为编辑器需要的editorStat

    }
    componentWillUnmount() {
        PubSub.unsubscribe("NoteInfo")
        // PubSub.unsubscribe("updateEditor")
    }

    // 在编辑器获得焦点时按下ctrl+s会执行此方法
    // 编辑器内容提交到服务端之前，可直接调用editorState.toHTML()来获取HTML格式的内容
    submitContent = async () => {
        let {currentNote,editorState} = this.state;
        console.log("保存的笔记信息",currentNote);
        if (!currentNote.hasOwnProperty("NoteId")) {
            message.warn("请选中一个笔记！")
            return;
        }
        message.loading({ content: '保存中...', key:'submit'});

        let htmlContent = currentNote.IsMarkdown ? currentNote.Content : editorState.toHTML();
        //updateNote
        // const result = await saveEditorContent(htmlContent)
        try {
            let result
            // result = await request("note/updateNote",{token:this.props.Token,NoteId:currentNote.NoteId,Usn:currentNote.Usn,Content:htmlContent})
            result = await request("note/updateNote",{token:this.props.Token,NoteId:currentNote.NoteId,Content:htmlContent})
            console.log("笔记内容的更新",result)
            /* result = await request("note/getNotes", {token: this.state.userToken,afterUsn:result.usn}, "post")
             console.log("同步笔记",result)*/
            if(!result.hasOwnProperty("Ok")){
                message.success({content:"保存成功",key:'submit',duration:2})
                // NoteInfo.Usn = result.Usn;
                currentNote.UpdatedTime = result.UpdatedTime;
                currentNote.Content = htmlContent
                //通知主界面全局更新笔记相关信息
                PubSub.publish("updateNote",currentNote)
                //头部组件更新
                PubSub.publish("NoteInfo",{
                    type:"update",
                    NoteInfo:currentNote
                })
                this.setState({currentNote});

            }else {
                message.warn({content:"保存失败请稍后再试",key:'submit',duration:2})
            }
        }catch (e) {
            console.log(e);
            if (e.hasOwnProperty("status")){
                message.warn({content:"保存失败",key:'submit',duration:2})
            }else {
                message.warn({content:"请求超时!",key:'submit',duration:2})
            }

        }

    }

    handleEditorChange = (editorState) => {
        console.log(editorState.toHTML());
        // console.log(editorState.toHTML());
        this.setState(({currentNote}) => {
            currentNote.Content = editorState.toHTML();
            return{
                editorState,
                currentNote,
            }
        });
    }
    onMarkDownChange = ({ target: { value } } )=> {
        this.setState(({currentNote}) => {
            currentNote.Content = value;
            return {currentNote};
        });
        // console.log(value);
    }
    /*
     <div className="MyBraftEditor">
                {
                    IsMarkdown ?
                        <div style={{
                            display:'flex',
                            padding:20,
                            width:'100%'
                        }}>
                            <div style={{width:'50%',maxHeight:"100%"}}>
                                <TextArea
                                    value={currentNote.Content}
                                    onChange={this.onMarkDownChange}
                                    placeholder={currentNote.Content ? currentNote.Content : "Mark Down"}
                                    style={{height:'100%',maxHeight:1200,}}
                                    // autoSize={{ minRows: 25}}
                                />
                            </div>
                            <div className='MarkDownContainer'>
                                <div className='MarkDown' >
                                    <ReactMarkdown
                                        rehypePlugins={[rehypeHighlight]}
                                    >
                                        {currentNote.Content}
                                    </ReactMarkdown>
                                </div>
                            </div>

                    </div> :  <div>
                                <BraftEditor
                                    value={editorState}
                                    excludeControls={['emoji']}
                                    onChange={this.handleEditorChange}
                                    onSave={this.submitContent}
                                    id="editor-with-code-highlighter"
                                />
                            </div>
                }

            </div>
     */
    render() {
        const { editorState,currentNote } = this.state
        let IsMarkdown = currentNote.IsMarkdown
        return (
            <div className="MyBraftEditor">
                {
                    IsMarkdown ?
                        <div style={{
                            display:'flex',
                            padding:20,
                            width:'100%'
                        }}>
                            <div style={{width:'50%',maxHeight:"100%"}}>
                                <TextArea
                                    value={currentNote.Content}
                                    onChange={this.onMarkDownChange}
                                    placeholder={currentNote.Content ? currentNote.Content : "Mark Down"}
                                    style={{height:'100%',maxHeight:1200,}}
                                    // autoSize={{ minRows: 25}}
                                />
                            </div>
                            <div className='MarkDownContainer'>
                                <div className='MarkDown' >
                                    <ReactMarkdown
                                        rehypePlugins={[rehypeHighlight]}
                                    >
                                        {currentNote.Content}
                                    </ReactMarkdown>
                                </div>
                            </div>

                        </div> :  <div>
                            <BraftEditor
                                value={editorState}
                                excludeControls={['emoji']}
                                onChange={this.handleEditorChange}
                                onSave={this.submitContent}
                                id="editor-with-code-highlighter"
                            />
                        </div>
                }

            </div>
        )

    }
}

export default MyBraftEditor;